@import url(https://fonts.googleapis.com/css?family=Raleway:500);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* text-align: center; */
  /* margin-top: 60px; */
  color: #393939;
}
h1, h2 {
  margin-left: 5%;
}
h1 {
  font-size: 5em;
}
h2 {
  font-size: 3em;
  margin-top: 5%;
}
body {
  margin: 0;
}
html {
  height: 100%;
  background-color: #d5d5d5;
}
.threeColGrid {
  display: grid;
  grid-template-columns: 1fr 2fr 3fr;
  grid-auto-rows: minmax(320px, auto);
  grid-gap: 1em;
}
.right {
  text-align: center;
}
.right img {
  max-width: 100%;
  max-height: 100%;
}
.projectList {
  list-style: none;
  background-color: white;
  padding-top: 16px;
  padding-bottom: 16px;
  margin-top: 0;
}
@media (max-width: 600px) {
.threeColGrid {
    display: block;
}
.projectList {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1em;
}
.projectList p {
    margin: 0;
}
.center {
    margin-bottom: 1em;
}
}

